
import VHeader from '@/components/v-header/v-header'
import imageTop from '@/assets/img/default_avatar.png'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from '@/api/axiosApi'
import { QINIU_PREFIX } from '@/api/config'

export default {
data () {
return {
imageTop: imageTop,
styles: {
height: '0px'
},
focusList: [],
foucusShow: true
}
},
methods: {
handleClickFocusDesc (following) {
this.$router.push('/meiTu/userDynamic/' + following)
},
handleClickFocus (following) {
axios.post(this, '/v1/follow/' + this.$route.params.userId + '/' + following + '/0', null, (data) => {
this.$vux.toast.text('取消关注成功', 'middle')
this.getFocusList()
})
},

getFocusList () {
axios.get(this, '/v1/follows', {follower: this.$route.params.userId, valid: 1}, (data) => {
data.forEach(item => {
if (item.followingHeadUrl) {
if (item.followingHeadUrl.indexOf(QINIU_PREFIX) > -1) {
item.followingHeadUrl += '-bheader'
}
} else {
item.followingHeadUrl = imageTop
}
})
this.focusList = data
})
}
},
created () {
this.cellContentWith = window.innerWidth - 55 - 95
this.getFocusList()
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
},
components: {
VHeader,
Scroller,
Cell,
CellBox,
imageTop,
XInput,
Calendar,
XTextarea,
XButton,
Group
}
}



